.CartDropdown {
  position: absolute;
  width: 350px;
  height: 500px;
  display: flex;
  flex-direction: column;
  padding: 20px 0;
  border: 1px solid #2b3a67;
  background-color: #e7e5e5;
  top: 61px;
  right: 0;
  z-index: 4;
}
.CartItems {
  padding-top: 5px;
  height: 400px;
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.CartDropdown button {
  margin: 1rem auto 0;
  font-size: 14px;
}

.CartDropdown button:disabled {
  opacity: 0.8;
}
.CartDropdown button:disabled:hover {
  background-color: #2b3a67;
}

.empty-cart {
  display: flex;
  flex-direction: column;
}

.empty-cart img {
  width: 210px;
  height: 180px;
  margin: 1rem auto 0;
}

.empty-cart span {
  font-size: 1.5rem;
  text-align: center;
}
